html {
  --input-padding-x: 0.5rem;
  --input-padding-y: 0.5rem; }

.card-login {
  width: 100%;
  max-width: 320px;
  margin: 0 auto; }
  .card-login .form-group:not(.profile-pic) {
    position: relative;
    margin-bottom: 1rem; }
    .card-login .form-group:not(.profile-pic) > input {
      padding: 0.5rem 0.5rem;
      padding: var(--input-padding-y) var(--input-padding-x); }
      .card-login .form-group:not(.profile-pic) > input:not(:placeholder-shown) {
        padding-top: calc( 0.5rem + 0.5rem * (2 / 3));
        padding-top: calc( var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
        padding-bottom: calc(0.5rem / 3);
        padding-bottom: calc(var(--input-padding-y) / 3); }
        .card-login .form-group:not(.profile-pic) > input:not(:placeholder-shown) ~ label {
          padding-top: calc(0.5rem / 3);
          padding-top: calc(var(--input-padding-y) / 3);
          padding-bottom: calc(0.5rem / 3);
          padding-bottom: calc(var(--input-padding-y) / 3);
          font-size: 12px;
          color: #777; }
      .card-login .form-group:not(.profile-pic) > input.invalid-field {
        border-color: #dc3545 !important; }
    .card-login .form-group:not(.profile-pic) > input::-webkit-input-placeholder {
      color: transparent; }
    .card-login .form-group:not(.profile-pic) > input::-ms-input-placeholder {
      color: transparent; }
    .card-login .form-group:not(.profile-pic) > input::placeholder {
      color: transparent; }
    .card-login .form-group:not(.profile-pic) > label {
      padding: 0.5rem 0.5rem;
      padding: var(--input-padding-y) var(--input-padding-x);
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      margin-bottom: 0;
      line-height: 1.5;
      color: #495057;
      border: 1px solid transparent;
      border-radius: 0.25rem;
      -webkit-transition: all 0.1s ease-in-out;
      transition: all 0.1s ease-in-out; }
  .card-login .form-group.profile-pic > input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1; }
  .card-login .form-group.profile-pic > label {
    border-radius: 0.3rem; }
  .card-login .form-group.profile-pic > input + label {
    cursor: pointer;
    font-size: 1em;
    font-weight: 700;
    color: white;
    background-color: #6c757d;
    display: inline-block; }
    .card-login .form-group.profile-pic > input + label * {
      pointer-events: none; }
  .card-login .form-group.profile-pic > input:focus + label,
  .card-login .form-group.profile-pic > input + label:hover {
    background-color: #5a6268; }
  .card-login .form-group.profile-pic > input:focus + label {
    outline: 1px dotted #6c757d;
    outline: -webkit-focus-ring-color auto 5px; }
  .card-login .form-group > .show-invalid {
    display: block !important; }

.alert {
  position: absolute !important;
  width: 100%; }
