html,
body,
#root,
.app {
  height: 100%;
  width: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.vertical-center {
  min-height: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center; }

.scroll-box {
  overflow-y: auto;
  height: 486px;
  padding: 0 !important; }
  .scroll-box::-webkit-scrollbar-thumb {
    background-color: #6c757d; }
  .scroll-box::-webkit-scrollbar, .scroll-box::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.03); }
  .scroll-box::-webkit-scrollbar {
    width: 11px; }

.btn > a {
  color: inherit; }
  .btn > a:hover {
    color: inherit;
    text-decoration: none; }

.btn:focus {
  box-shadow: none !important; }

.user-card {
  margin: 20px; }
  .user-card .user-card-details p.one-liner {
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .user-card .user-card-details .card-text {
    font-size: 0.8em; }

.initial-loading-bar {
  background-color: #007bff; }

.home-loading-bar {
  background-color: #6c757d; }

.initial-loading-bar,
.home-loading-bar {
  height: 2px; }

@media (min-width: 768px) {
  .user-card .user-card-details {
    border-left: 1px solid #eee; } }
